import React ,{useState}from 'react'
import { connect } from 'react-redux';
import {commodity} from './actionCreator'
const Login =(props)=>{
    props.dispatch(commodity())
    const log = () => {
        localStorage.setItem('userinfo','tom')
        if((props.location.search) === '?details'){
            window.location.href = './details';
        }
        if((props.location.search) === '?collection'){
            window.location.href = './collection';
        }
    }
    return (
        <div style={{textAlign:'center',marginTop:'20%'}}>
            <p style={{color:'orangered',fontSize:'40px'}}>请先登录</p>
            <input type='text' placeholder='输入您的用户名' 
                    style={{height:'35px',width:'300px',border:'0.1px solid',marginTop:'5%'}}>
            </input>
            <input type='text' placeholder='输入您的密码' 
                    style={{height:'35px',width:'300px',border:'0.1px solid',marginTop:'5%'}}>
            </input>
            <button onClick={log} style={{width:'100px',height:'50px',marginTop:'10%',marginLeft:'10%'}}>
              登录
            </button>
        </div>
    )
}

const mapStateToProps = (state) => ({
    username: state.home.username
})
export default connect(mapStateToProps)(Login);
