import React from 'react'
import { SearchBar } from 'antd-mobile'

const Search = () => {
    return (
        <div>
            <SearchBar 
                placeholder='寻找宝贝店铺' 
                showCancelButton="{{true}}"
                onCancel={()=>window.history.back(-1)}
            />
                
        </div>
    )
}

export default Search;