import { SearchBar } from 'antd-mobile'
import React from 'react'
import { Link} from 'react-router-dom'
import Animation from './Animation'
import Mytab from './Mytab'
import {commodity} from './actionCreator'
import { connect } from 'react-redux';
import './Home.css'
const Home = (props) => {

    props.dispatch(commodity())
    return (
        <div>
            <Link to='/search'> 
                <SearchBar
                    placeholder='寻找宝贝店铺' 
                    show-cancle="false" 
                />
            </Link>
            <Animation />
            <p style={{color:'orangered',textAlign:'center',marginTop:10}}>你可能还喜欢</p>
            <div className="imgstyle">
                {  
                    (props.goods.length==0)
                    ?null
                    :props.goods.map((item,index)=>{
                        return (
                            <div>
                                <Link to={{pathname:'/details',state:{index}}}>
                                    <img src={item.img}></img>
                                    <p className='decription'>{item.title}</p>
                                    <p className='price'>{item.price}</p> 
                                </Link>
                            </div> 
                        )
                    },this)
                }
            </div>
            <Mytab />
        </div>
    )
}

const mapStateToProps=(state)=>({
    goods:state.home.datalist,
})

export default connect(mapStateToProps)(Home);