import React from 'react'
import {Carousel,WingBlank} from 'antd-mobile'
import './Animation.css'


class Animation extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 176,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['1', '2', '3','4','5','6'],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay
          infinite
        >
          <img src='https://gw.alicdn.com/imgextra/i2/87/O1CN015w4ZpM1CVt5SJMwF8_!!87-0-lubanu.jpg' alt='' />
          <img src='https://aecpm.alicdn.com/simba/img/TB1CWf9KpXXXXbuXpXXSutbFXXX.jpg_q50.jpg' alt='' />
          <img src='https://gw.alicdn.com/imgextra/i3/191/O1CN01VWfpT01DHWHQurTZ7_!!191-0-lubanu.jpg' alt='' />
          <img src='https://img.alicdn.com/tps/i4/https://img.alicdn.com/imgextra/i2/6000000007999/O1CN01p1MVOa28xalDbCnhY_!!6000000007999-0-octopus.jpg' alt='' />
          <img src='https://aecpm.alicdn.com/simba/img/TB15tIjGVXXXXcoapXXSutbFXXX.jpg' alt='' />
          <img src='https://gw.alicdn.com/imgextra/i2/164/O1CN01CtnlsR1D59aOtvXyg_!!164-0-lubanu.jpg' alt='' />
          
        </Carousel>
      </WingBlank>
    );
  }
}

export default Animation;