import React from 'react'
import {TabBar, WingBlank} from 'antd-mobile'
import './Detail.css'
import { connect } from 'react-redux';
import {commodity} from './actionCreator'

const Details = (props) => {
    props.dispatch(commodity());
    if(props.location.state){
        localStorage.setItem("index",props.location.state.index);
    }    
    const m = localStorage.getItem("index");
    let i=0;//判断是否已加入收藏;0表示没有加入
    if(props.shoucang.length !== 0){
        props.shoucang.map((item)=>{
            if(m === item){
                i = 1;
            };
        });
    };
    function con(){
        if(!localStorage.getItem('userinfo')){
            window.location.href = './login?details';
        }
        if(i !== 1){
            props.dispatch({
                type:'ADD_TODO',
                value:m
            })
        };
    };
    return (
        <div>
            <TabBar>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-fanhuijiantou'></i>                           
                        }
                        onPress={ () =>
                            props.history.push('/')
                         }
                    >
                    </TabBar.Item>
            </TabBar>
            <WingBlank>
                {
                    (props.goods.length === 0)
                    ?null
                    :
                    <div>
                        <img src={props.goods[m].img} style={{width:'350px'}}></img>
                        <p className='decription'>{props.goods[m].title}</p>
                        <p className='price'>{props.goods[m].price}</p> 
                    </div> 
                }
            </WingBlank>
            <div className='coll'>
            <div className='colltab'>
                <TabBar
                     
                        unselectedTintColor="#949494"
                        tintColor="orangered"
                        barTintColor="white"
                        
                    >
                        <TabBar.Item
                            title="店铺"
                            key="marker"
                            icon={
                                <i className='iconfont icon-dianpu'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-dianpu1'></i>                           
                            }
                        ></TabBar.Item>
                        <TabBar.Item
                            title="客服"
                            key="marker"
                            icon={
                                <i className='iconfont icon-kefu'></i>                           
                            }
                            selectedIcon={
                                <i className='iconfont icon-kefu1'></i>                           
                            }
                        ></TabBar.Item>
                </TabBar>
            </div>
            <div style={{float:'right'}}>
            <button className='button1'
                onClick={()=>{
                    con();
                }}
            >
                {
                    (i===0)
                    ?<i className='iconfont icon-shoucang'></i>
                    :<i className='iconfont icon-collection-b'></i>
                } 
            </button>
            <button className='button2'>加入购物车</button>
            <button className='button3'>立即购买</button>
            </div>
            </div>
        </div>
    )
}

const mapStateToProps=(state)=>{
    return {goods:state.home.datalist,
    shoucang:state.shoucang}
}

export default connect(mapStateToProps)(Details);