//reducer是一个纯函数，没有副作用，有相同的输入，一定会有相同的输出
let shoucang = [];
//更新状态
function todolist(state=shoucang,action){
    if(action.type=='ADD_TODO'){
        //不可变对象
        return [...state,action.value]
    }
    if(action.type=='DEL_TODO'){
        // action.index
        let arr = [...state];
        arr.splice(action.index,1);
        return arr;
    }
    return state;
}
export default todolist;