import React from 'react'
import Mytab from './Mytab'
import './Home.css'
import {commodity} from './actionCreator'
import { connect } from 'react-redux';
import { Button } from 'antd-mobile';


const Collection = (props) => {

    props.dispatch(commodity());
    return (
        <div>
            <div className="imgstyle">
            {  
                (props.goods.length === 0)
                ?null
                :
                    (props.shoucang.length==0)
                    ?null
                    :props.shoucang.map((item,index)=>{
                        return (
                            <div>
                                <img src={props.goods[item].img}></img>
                                <p className='decription'>{props.goods[item].title}</p>
                                <p className='price'>{props.goods[item].price}</p> 
                                <Button onClick={()=>{
                                    props.dispatch({
                                        index:index,
                                        type:'DEL_TODO',
                                    })
                                }}>取消收藏</Button>
                            </div> 
                        )
                    },this)
            }
            </div>
            <Mytab />
        </div>
    )
}
const mapStateToProps=(state)=>{
    return {
        shoucang:state.shoucang,
        goods:state.home.datalist
    }
}

export default connect(mapStateToProps)(Collection);